	getters: {
		invites: (state) => state.invites.all.filter((invite) => ["oneOnOne", "oneOnMany"].includes(invite.type)),

		polls: (state) => state.invites.all.filter((invite) => invite.type == "group"),

		events: (state) => state.events.all,
	},
	mutations: {
		setEvent(state, event) {
			state.events.one = event;
		},
		newInvite(state) {
			let invite = JSON.parse(newInviteData);

			let start = moment().utc().startOf("date");

			let end = start.clone().add(7, "d");

			invite.dateRange = { type: "days", start, end };

			invite.availability = state.auth.user.defaultWeekAvailability.map((availabile) => {
				let { rrule, duration } = availabile;

				rrule = rrule.replace(/DTSTART:YYYYMMDDTHHmmssZ/g, "DTSTART:" + start.clone().locale("en").format("YYYYMMDDTHHmmss") + "Z");

				rrule = rrule.replace(/UNTIL=YYYYMMDDTHHmmssZ/g, "UNTIL=" + end.clone().locale("en").format("YYYYMMDDTHHmmss") + "Z");

				return { duration, rrule };
			});

			state.invites.one = invite;
		},
		setInvite(state, invite) {
			for (let field in invite) {
				state.invites.one[field] = invite[field];
			}
		},
		setLocale(state, locale) {
			if (state.locale != locale) {
				state.locale = locale;
				sessionStorage.setItem("locale", locale);
				// reload page to apply moment locale
				window.location.reload();
			}
		},

		setGoogleRedirect(state, data) {
			state.auth.authBtns = state.auth.authBtns.map((btn) => {
				if (btn.provider == "google") {
					btn.redirect = data.authorization_url;
				}

				return btn;
			});
		},

		setGoogleAuth(state, data) {
			console.log(data);
		},
	},
	actions: {
		getEvent({ commit, state }, id) {
			let event = state.events.all.find((event) => event.id == id);

			commit("setEvent", event);
		},

		async getGoogleRedirect({ commit }) {
			let url = "https://mohammedhijje.online/api/v1/auth/social/o/google-oauth2/?redirect_uri=https://mohammedhijje.online/api/v1/rest/v1/redirect/";
			// let url = "https://mohammedhijje.online/api/v1/auth/social/o/google-oauth2/?redirect_uri=http://localhost:8080/signup?code=aaaaaaaaaa&state=aaaaaaaaaa";
			// "https://accounts.google.com/o/oauth2/auth?client_id=384540384493-vfcl8ae3n46k39ets6rsff3r9up8rups.apps.googleusercontent.com&redirect_uri=https://mohammedhijje.online/api/v1/rest/v1/redirect/&state=PtQWElzw5iOGXSElzNQ37InJdzjNDkxE&response_type=code&scope=https://www.googleapis.com/auth/userinfo.email+https://www.googleapis.com/auth/userinfo.profile+openid+https://www.googleapis.com/auth/calendar.events+https://www.googleapis.com/auth/calendar+https://www.googleapis.com/auth/calendar.calendarlist+openi‏";

			let data = await axios.get(url);

			commit("setGoogleRedirect", data);
		},

		async googleLogin({ commit }, { code, state }) {
			let url = `https://mohammedhijje.online/api/v1/auth/social/o/google-oauth2/`;

			if (!state || state == "") return;

			url = `${url}?code=${code}&state=${state}`;

			let data = await axios.post(url);
			// let data = await axios.post(url, { code, state });

			commit("setGoogleAuth", data);
		},
	},
	modules: {},
